body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  box-sizing: border-box;
}


header {
  display: -webkit-flex;
  display: flex;
  height: 80px;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  border-bottom: 1px solid #a9adac;
}

header h1 {
  font-size: 2rem;
  color: #e27a3f;
}

/* Main.css */

.main {
  display: -webkit-flex;
  display: flex;
}

/* Selection.css */

.selection {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-bottom: 60px;
}

.selection label {
  margin-bottom: 10px;
  color: #555;
  font-size: 1rem;
}

.selection select {
  max-width: 150px;
}

/* Slider.css */
.slider {
  margin-bottom: 40px;
}

.slider label {
  color: #555;
  font-size: 1rem;
  margin-bottom: 20px;
  display: block;
}

.input-range__slider {
  background: #e23a3f;
  border: none
}


.input-range__track {
  background: #ddd;
}

.input-range__track--active {
  background: #e27a3f;
}

.input-range__label--value .input-range__label-container {
  background: #e27a3f;
  color: white;
  font-size: 0.65rem;
  padding: 2px 5px;
  border-radius: 2px;
}

.input-range__label--min .input-range__label-container,
.input-range__label--max .input-range__label-container {
  font-size: 0.65rem;
  color: #888;
  left: 0;
}

.input-range__label--max .input-range__label-container {
  left: 25%;
}

/* SearchButton.css */
.search-button {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.search-button button {
  padding: 10px 21px;
  background: #e27a3f;
  color: rgba(255,255,255,0.75);
  font-size: 1rem;
  cursor: pointer;
  transition: color 0.2s ease-out;
  outline: 0;
  border: 0;
}

.search-button button:hover {
  color: white;
}

/* Navigation.css */

.navigation {
  -webkit-flex-basis: 20%;
          flex-basis: 20%;
  min-width: 300px;
  padding: 20px;
}

/* Movies.css */

.movies {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-flex-basis: 80%;
          flex-basis: 80%;
  padding: 20px;
  margin: 0;
}

.pagination {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 40px 20px;
}

/* MovieListItem.css */

.movie-item {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex-basis: 22%;
          flex-basis: 22%;
  list-style: none;
  margin: 1.5%;
  padding: 20px;
  box-sizing: border-box;
  border: 1px solid #eee;
  box-shadow: 0px 10px 28px -7px rgba(0,0,0,0.1);
  /* background: lavender; */
}

.movie {
  list-style: none;
}

.movie-item img {
  width: 100%;
}

.movie-description {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  padding: 10px;
}

.movie-description h2 {
  color: #555;
  font-weight: bold;
  margin-bottom: 20px;
}

.movie-details {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.movie-details span {
  color: #555;
  font-weight: bold;
  font-size: 0.8rem;
}


.movie-year,
.movie-rating {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.movie-year .title,
.movie-rating .title {
  color: #aaa;
  margin-bottom: 5px;
  font-size: 0.65rem;
  font-weight: normal;
}

.movie-rating {
  -webkit-align-items: flex-end;
          align-items: flex-end;
}

.thumbnail {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  /* cursor: pointer; */
  text-decoration: none;
}

/* // Movie.css */

.movie-page {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  /* border: 1px solid red; */
}

.movie-page h5 {
  color: #888;
  font-weight: normal;
  line-height: 1.25rem;
}

.movie-page h5 span {
  color: black;
  font-weight: normal;
  font-size: inherit;
  padding-left: 1rem;
}

.movie-page h4 {
  margin: 0;
  font-size: 1.25rem;
  line-height: 2;
}

.movie-page p {
  color: #888;
  line-height: 1.5;
}

.movie-page .movie-backdrop {
  -webkit-flex-basis: 100%;
          flex-basis: 100%;
  height: 575px;
  background-size: cover;
  background-position: center center;
}

.movie-page .movie-details {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  max-width: 800px;
  margin: 20px auto 60px auto;
}

.movie-page .movie-details h1 {
  line-height: 1.5em;
}

.movie-page .movie-details h1 span {
  color: #bbb;
  font-weight: normal;
  font-size: inherit;
  padding-left: 1rem;
  line-height: inherit;
}

.movie-page .genres {
  display: -webkit-flex;
  display: flex;
  margin-bottom: 1rem;
}

.movie-page .genres span {
  font-weight: normal;
}

.movie-page .separator {
  color: lightgray;
  padding: 0 10px;
}

